*{
  font-family: Arial, Helvetica, sans-serif;
  color: white;
}

body{
  background-color: #21232A;
  padding: 60px;
}

input{
  background: #444;
  border-radius: 150px;
  color: #ccc;
  font-size: 1.2rem;
  outline: none;
  padding-left: 10px;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid #ccc;
  border-radius: 6px;
  box-shadow: 0px 0px 25px black;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}